









































import AppCheckbox from "../AppCheckbox.vue";
import { toRefs } from "@vue/composition-api";
import { Component, Prop, Vue } from "vue-property-decorator";
import svgTrash from "@/assets/icons/trash.svg";
import useModal from "@/compositions/useModal";
import { ModalName } from "@/types/modal.enum";
import {
  useApiDriverDeleteCard,
  useApiDriverSetDefaultCard,
} from "@/api/driver";
import { errorHandler } from "@/helpers/error-handler";
interface Card {
  number: string;
}
interface IProps {
  [key: string]: any;
  value: Array<Card>;
  driverId: number;
}
@Component({
  setup(props: IProps, { emit }) {
    const { value, driverId } = toRefs<IProps>(props);
    const addCard = () => {
      const { showByName } = useModal();
      showByName(ModalName.addCard, {
        props: {
          id: driverId.value,
          type: "driver",
        },
        on: {
          send: () => {
            emit("addedCard");
          },
        },
      });
      return;
    };
    const {
      exec: removeCard,
      error: deleteError,
      result,
    } = useApiDriverDeleteCard({
      toast: { error: errorHandler(), success: () => "Карта успешно удалена!" },
    });
    const onRemoveCard = async (card: any) => {
      await removeCard({
        cardId: card.id,
        id: driverId.value,
      });
      if (deleteError.value) return;
      emit("removedCard");
    };
    const {
      exec: setDefaultCard,
      error: setDefaultError,
    } = useApiDriverSetDefaultCard({
      toast: {
        error: errorHandler(),
        success: () => "Карта по умолчанию установлена!",
      },
    });
    const onChangeDefault = async (card: any, value: boolean) => {
      await setDefaultCard({ cardId: card.id, id: driverId.value });
      if (setDefaultError.value) return;
      emit("setDefault");
    };
    return {
      onChangeDefault,
      addCard,
      onRemoveCard,
    };
  },
  components: {
    svgTrash,
    AppCheckbox,
  },
})
export default class DriverListCards extends Vue {
  @Prop({
    type: Array,
  })
  value: Array<Card>;

  @Prop({
    type: Boolean,
    default: true,
  })
  canEdit: boolean;
  @Prop(Number) driverId: number;
}
